import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { loadCharacterComics } from '../../actions';
import { get } from 'lodash/object';
import { ListItem, ListItemText, ListItemAvatar, Typography, Avatar } from "@material-ui/core";
import history from "../../routers/history";
import { FixedSizeList } from "react-window";

export default function CharacterComics({ id }) {
    const pagination = useSelector(state => state.pagination);
    const dispatch = useDispatch();
    const comicsIds = get(pagination, `characterComics[${id}].ids`, []);
    const nextPageUrl = get(pagination, `characterComics[${id}].nextPageUrl`, null);
    const comics = useSelector(state => state.entities.comics);
    // Meterlo en un promise para mejorar rendimiento(?
    const comicsToRender = [];
    comicsIds.forEach(id => {
        comicsToRender.push(comics[id]);
    });

    const renderRow = ({ style, index }) => {
        const comic = comicsToRender[index];
        return (
            <ListItem button style={{
                ...style,
                backgroundColor: index%2 === 0 ? 'white' : 'rgba(0,0,0,0.1)'
            }} key={`comic-${index}`} onClick={()=>{
                history.push(`/comics/${comic.id}`);
            }}>
                <ListItemAvatar>
                    <Avatar src={`${comic.thumbnail.path}.${comic.thumbnail.extension}`} />
                </ListItemAvatar>
                <ListItemText
                    disableTypography
                    primary={<Typography noWrap>
                        {`${comic.title}`}
                    </Typography>}
                />
            </ListItem>
        );
    };

    const handleLoadMore = () => {
        dispatch(loadCharacterComics(id, nextPageUrl !== null));
    };

    return (
        <div>
            <h2>Comics</h2>
            <FixedSizeList height={50 * Math.min(20, comicsToRender.length)} width="100%" itemSize={50} itemCount={comicsToRender.length}>
                {renderRow}
            </FixedSizeList>
            {nextPageUrl !== null && <button onClick={handleLoadMore}>Load more</button>}
        </div>
    );
}

